
/**
 * Retorna el input si todo está ok, sino devuelve la fecha pasada como parámetro.
 */
String.prototype.ValidDate = function ValidDate(dateToReturnWhenInputIsInvalid) {
    try {
        if (this.length != 10) return dateToReturnWhenInputIsInvalid;
        if (this.substring(2, 3) != "/") return dateToReturnWhenInputIsInvalid;
        if (this.substring(5, 6) != "/") return dateToReturnWhenInputIsInvalid;
    
        var days = parseInt(this.substring(0, 2));
        var months = parseInt(this.substring(3, 5));
        var years = parseInt(this.substring(6, 10));
    
        if(!(days > 0 && days <= 31)) return dateToReturnWhenInputIsInvalid;
        if(!(months > 0 && months <= 12)) return dateToReturnWhenInputIsInvalid;
        if(!(years > 1800 && years <= 3000)) return dateToReturnWhenInputIsInvalid;
        
        return this;
    } catch (e) {
        console.log(e);
        return dateToReturnWhenInputIsInvalid;
    }
};
