<template>
  <div class="ContenidoComponente">
    <apexcharts
      width="100%"
      :type="this.type"
      :options="this.chartOptions"
      :series="this.series"
      class="EstilosGrafico"
    ></apexcharts>
    <br />
  </div>
</template>

<script>
import VueApexCharts from "vue-apexcharts";

export default {
  name: "Grafico",
  components: {
    apexcharts: VueApexCharts,
  },
  watch: {
    DataEjeY: {
      // the callback will be called immediately after the start of the observation
      immediate: true, 
      deep: true,
      handler (val, oldVal) {
        // do your stuff
        this.ActualizarGrafico();
      },
    }
  },
  data: function() {
    //console.log(this.$props);
    return {
      //Grafico
      chartOptions: {
        chart: {
          id: "basic-bar",
        },
        xaxis: {
          categories: [],
          labels: {
            show: true,
            rotate: -45,
            rotateAlways: true,
          },
        },
        yaxis: {
          title: {
            text: "this.DataEjeY",
          },
        },
        noData: {
          text: "Aún no hay datos...",
        },
      },
      series: [
        {
          name: this.TituloEjeX,
          data: [],//this.$props.DataEjeY, //.UltimosCincoItems(),
        },
      ],
    };
  },
  // props: ['TituloEjeY', 'DataEjeX', 'DataEjeY', "Acumulativo", "type"],
  props: {
    TituloEjeY: String,
    DataEjeX: Array,
    DataEjeY: Array,
    Acumulativo: Boolean,
    type: {
      type: String,
      default: 'bar'
    },
  },
  mounted() {
    this.ActualizarGrafico();
    //console.log("asda 1");
    // this.SepararClaveValor();
    //console.log(this.$props.DataEjeX);
  },
  methods: {
    ActualizarGrafico(){
      this.chartOptions = {
        chart: {
          toolbar: {
            show: false
          },
        },
        xaxis: {
          categories: this.DataEjeX,
        },
        yaxis: {
          label:{
            style: {
              background: '#000',
              color: '#777',
              fontSize: '19px',
              fontWeight: 400,
            },
          },
        },
        plotOptions: {
          bar: {
            horizontal: false,
            dataLabels: {
              position: 'top',
            },
          }
        },
        dataLabels: {
          enabled: true,
          offsetY: 0,
          style: {
            fontSize: '12px',
            colors: ['#000']
          },
          formatter: function (val) {
            if (val <= -5) return "...";
            return val;
          },
        },
      };

      let _Acumulativo = []; var _Aux = 0;
      if (this.Acumulativo){
        this.DataEjeY.forEach(_Reg => {
          _Aux += _Reg;
          _Acumulativo.push(_Aux);
        });
        this.series = [
          {
            name: "Individual",
            data: this.DataEjeY,
          },
          {
            name: "Acumulativo",
            data: _Acumulativo,//this.$props.DataEjeY, //.UltimosCincoItems(),
          },
        ];
      }else{
        this.series = [
          {
            name: "Altura napa",
            data: this.DataEjeY
          }
        ];
      }
    },
  },
};
</script>


<style lang="scss" scoped>
  .ContenidoComponente{
    display:flex;
    align-items: center;
    justify-content: center;
  }
  .EstilosGrafico{
    width:calc(100% - 20px);
    max-width: 600px;
  }
  @media (max-width: 360px) {
    .EstilosGrafico{
      width:calc(100% - 10px);
      max-width:360px;
    }
  }
</style>